@import 'rplm-authority.variables';

.rplm-authority {
  background-color: $authority-background-color;
  padding: $authority-padding;

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__message {
    color: $authority-message-color;
    width: 75%;
  }

  &__sources {
    width: 25%;
  }
}
